
.app-wrapper {
    display: grid;
    margin: 0 auto;
    width: 900px;
    height: 100vh;
    grid-template-areas:
     "h h"
      "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}
.app-wrapper-content {
    width: 900px;
    height: 100vh;
    grid-area: c;
    background-color: cornflowerblue;
}



